import './RestComment.css';
import '../../StaticElement/Static.css';
import Carousel from '../../StaticElement/Component/Carousel/Carousel';
import RestCommentModal from './RestCommentModal';
import { useState } from 'react';

export default function RestComment(props){
    const [Display, setDisplay] = useState(false);
    const cards = [
        {
            id: '1',
            name:'',
            text:'      !            !           ,       !!!!     !',
            kitchen: 3,
            comfort: 5,
            service: 5,
        },
        {
            id: '2',
            name:'2',
            text:'      !            !           ,       !!!!     !',
            kitchen: 4,
            comfort: 5,
            service: 5,
        },
        {
            id: '3',
            name:'3',
            text:'      !            !           ,       !!!!     !',
            kitchen: 1,
            comfort: 5,
            service: 5,
        },
        {
            id: '4',
            name:'4',
            text:'      !            !           ,       !!!!     !',
            kitchen: 2,
            comfort: 5,
            service: 5,
        },
        {
            id: '5',
            name:'5',
            text:'      !            !           ,       !!!!     !',
            kitchen: 5,
            comfort: 5,
            service: 5,
        },
    ];

    return(
        <div className='RestCommentCont' id='Comment'>
        <RestCommentModal display={Display} setDisplay={setDisplay} Name={props.Name}/>
            <div className='RestCommentTitle'>
                <div></div>
                <hr color='Black' width='30%' style={{margin:'0px', border:'0.01px black solid', marginTop:'1vw'}}/>
            </div>
            <Carousel sw={2} cards={cards}/>
            <div className='RestMenuShowButton' onClick={() => setDisplay(true)}> </div>
        </div>
    )
}