import './Gallery.css';
import '../../StaticElement/Static.css';
import PhotoCard from './PhotoCard';

export default function Gallery(){
    return(
        <div className='GalleryCont' id='Gallery'>
        <div className='GalleryTitleOne'></div>
        <div className='GalleryTitleTwo'></div>
        <div className='GalleryGridCont'>
            <div className='GalleryGridContInsideOne'>
                <PhotoCard img={require('../../StaticElement/Image/PhotoCard1.webp')} width='100%' height='200%' title='The view' text=' 3d '/>
                <PhotoCard img={require('../../StaticElement/Image/PhotoCard2.webp')} width='100%' height='200%' title=''/>
            </div>
            <div className='GalleryGridContInsideTwo'>
                <PhotoCard img={require('../../StaticElement/Image/PhotoCard3.webp')} width='100%' height='105%' title='The view'/>
                <PhotoCard img={require('../../StaticElement/Image/PhotoCard4.webp')} width='100%' height='105%' title=''/>
                <PhotoCard img={require('../../StaticElement/Image/PhotoCard5.webp')} width='100%' height='190%' title=' '/>
            </div>
            <div className='GalleryGridContInsideThree'>
                <PhotoCard img={require('../../StaticElement/Image/PhotoCard6.webp')} width='100%' height='200%' title='The view'/>
                <PhotoCard img={require('../../StaticElement/Image/PhotoCard7.webp')} width='100%' height='200%' title=''/>
            </div>
        </div>
        <button className='GalleryButton'></button>
    </div>
    )
}